<template>
  <div class="login">
    <div class="login-box-bg hidden" id="login-box-bg"></div>
    <div class="login-box-wrapper">
      <div class="login-box smaller" id="login-box">
        <div class="box-title">
          <span class="icon-key"></span> Login SSO
        </div>
        <div class="box-body">
          <div class="errormsg" v-html="this.error"></div>
          <form @submit.prevent="handleFormSubmit">
            <fieldset>
              <div class="errormsg hidden">We could not log you in</div>
              <div class="login-box-row label">
                <input
                  type="text"
                  id="username"
                  name="username"
                  v-model="userName"
                  autocomplete="off"
                  placeholder="E-mail address"
                />
                <div class="username-question" v-tooltip="usernameHelp">
                  <span class="icon-help-round"></span>
                </div>
              </div>
              <div class="login-box-row buttons">
                <div class="loader" v-show="loading"></div>
                <input
                  type="submit"
                  value="Submit"
                  class="button green"
                  id="loginButton"
                  v-show="!loading"
                  v-cloak
                />
              </div>
              
            </fieldset>
          </form>
        </div>
      </div>
    </div>
    
    <CitrixLogin />
  </div>
</template>


<script>
import CitrixLogin from '@/components/CitrixLogin.vue'
import Header from '@/components/Header.vue'
import Footer from '@/components/Footer.vue'

export default {
  name: "LoginSSO",
  components: {
    CitrixLogin,
    Header,
    Footer
  },
  data() {
    return {
      usernameHelp:
        "This can be your username, email, or membership number (but not your surname).",
      error: "",
      loading: false,
      userName: "",
      redirectUrl: "",
      emailUser: null
    };
  },
  computed: {},
  methods: {
    populatePage() {
      this.loading = true;
      if (
        typeof this.$route.query != "undefined" &&
        typeof this.$route.query.redirectUrl != "undefined"
      ) {
        this.redirectUrl = this.$route.query.redirectUrl;
      }
      this.$ajax
        .post("/auth/PopulatePage", {
          RedirectUrl: this.redirectUrl
        })
        .then(resp => {
          if (typeof resp.data != "undefined") {
            if (resp.data.Error) {
              this.error = resp.data.Error;
            } else if (resp.data.ClientRedirect && resp.data.RedirectUrl) {
              window.location.href = resp.data.RedirectUrl;
            } else if (resp.data.RedirectPage != null) {
              var params = {};
              if (
                resp.data.RedirectPage == "SetupQuestions" &&
                resp.data.Questions.length
              ) {
                params = { questions: resp.data.Questions };
              }
              this.$router.push({
                name: resp.data.RedirectPage,
                params: params,
                query: { redirectUrl: resp.data.RedirectUrl }
              });
            }
          }
        })
        .catch(() => {
          this.loading = false
        })
        .then(() => {
          this.loading = false;
        });
    },
    handleFormSubmit() {
      let data = {
        email: this.userName,
      };
      this.error = "";
      if (data.email == "") {
        this.error = "E-mail address cannot be blank.";
        return;
      }

      this.loading = true;

      this.$ajax
        .post("/Auth/Loginsso", data)
        .then(resp => {
          if (resp.data) {
            window.location.href = resp.data;
          } else {
            this.error = "E-mail address not found.";
            this.loading = false;
          }
        })
        .catch(() => {
          this.loading = false;
        })
    }
  },
  mounted() {
    this.populatePage();
  }
};
</script>


