<script>
import Setup from './Setup.vue'
//TODO: run a client-side check to see if token and userid is present. If not, just don't display the page. Or error...
export default {
    name: "SetNew",
    props: ['setPassToken', 'pageSubtitle', 'userId'],
    data() {
        return{
            error: "",
            loading: false,
            redirectUrl: "",
            output: {
                password: "",
                confirmPassword: "",
            }
        }
    },
    components: {
        Setup,
    },
    methods: {
        handleFormSubmit(){
            let data = {
                password: this.output.password,
                confirmPassword: this.output.confirmPassword,
                userId: this.userId,
                token: this.setPassToken
            };
            this.error = "";
            if(data.password == "" && data.confirmPassword == ""){
                this.error = "Password fields cannot be blank.";
            }else if(data.confirmPassword == ""){
                this.error = "Confirm Password field cannot be blank.";
            }else if(data.password == ""){
                this.error = "Password field cannot be blank.";
            }else if(data.password != data.confirmPassword){
                this.error = "Password fields do not match.";
            }
            if(this.error != "") return;

            this.loading = true;

            this.$ajax.post('/Password/SetNew', data)
            .then(resp => {
                if (typeof resp.data != "undefined"){
                    if(resp.data.Error != null) {
                        this.error = resp.data.Error;
                    }else if(resp.data.RedirectPage != null){
                        this.$router.push({name: "Login", query: {redirectUrl: this.redirectUrl}});
                    }
                }
            })
            .catch(() => {})
            .then(() => {
                this.loading = false;
            });
        }
    },
    mounted() {
        if (typeof this.$route.query != "undefined"){
            if(typeof this.$route.query.redirectUrl != "undefined") {
                this.redirectUrl = this.$route.query.redirectUrl;
            }
        }
        // this.$reformatUrl("SetNew");
    }
}
</script>

<template>
    <setup title="Set New Password" :error="error">
        <form method="post" @submit.prevent="handleFormSubmit">
            <h3>{{pageSubtitle}}</h3>
            <passwordPage :loading="loading" v-model="output"></passwordPage>
        </form>
    </setup>
</template>
